.Navbar {
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 30px;
  text-align: center;
}

#title {
  background-color: transparent;
  border: none;
  outline: none;
  box-sizing: border-box;
  color:black;
  font-size: 27px;
  height: 35px;
  left: 50%;
  margin: -25px 0 0 -100px;
  padding: 10px 0px;
  top: 50%;
  width: 680px;
}

#body {
  background-color: transparent;
  border : 5px solid rgb(56, 52, 52);
  resize: none;
  margin-top: 100px;
  font-size : 20px;
}

#button {
  cursor:pointer;
  background: black;
  color: white;
  width: 150px;
  height: 30px;
  font-size: 18px;
  border-radius: 30px;
}

#button:hover {
  color:yellow;
}


form {
  text-align: center;
  position: relative;
  align-content: space-between;
}

.posts {
  display: grid;
  place-items: center;
}

.listnull {
  margin-top: 200px;
  background-color: white;
  border: 2px solid black;
  color: black;
  height: 88px;
  width: 100%;
  border-radius: 22px;
}
.listnull h1{
  text-align: center;
}

.contextControl {
  margin-top: 5px;
  text-align: center;
}
.contextControl button {
  margin-left: 10px;
}

.read {
  float: right;
  width: 400px;
  height: 650px;
  border: solid 2px black;
  margin-right: 30px;
}

.read h2 {
  text-align: center;
  padding-left: 15px;
}

.read ol li {
  font-size: 20px;
}

.read a {
  text-decoration: none;
  background-color: black;
  color: white;
  display: inline-block;
  width: 325px;
  height: 25px;
  text-align: center;
  margin: 8px;
  font-size: 20px;
  border-radius: 30px;
}

.read a:hover { 
  color: yellow;
  text-decoration: underline;
}

.miniroom {
  float : right;
  margin-right: 20px;
  border: 3px solid black;
}

.header {
  background: rgb(56, 52, 52);
  width: 182px;
  height: 100%;
}

input::placeholder {
  color:darkgrey;
}

textarea::placeholder {
  color:darkgrey;
}

@font-face {
  font-family: 'EarlyFontDiary';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_220508@1.0/EarlyFontDiary.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

body, #title, #body, .posts {
  font-family: 'EarlyFontDiary';
}

body {
  background-color: rgb(238, 238, 238);
  overflow-x: hidden;
}


.bg {
  text-align: center;
  float: left;
  margin-left: 335px;
  width:1080px;
  height:640px;
  background-image : url('./img/3.jpg');
  background-size : cover;
  align-items: center;
  justify-content: center;
}
